import React, { Fragment } from 'react';
import Cards from './Cards'

const CardList = ({ robots }) => {
    return (
        <Fragment>
            
            {robots.map((user, i) => {
                return (
                    <Cards
                        key={i}
                        id={robots[i].id}
                        name={robots[i].name}
                        email={robots[i].email} />
                     );
                 }
            )
            }
        </Fragment>
    );
}


export default CardList;