import React, { Fragment } from 'react';

const Cards = ({name, email, id}) => {
return(
    <Fragment>
    <div className="tc bg-navy dib br3 pa3 ma2 grow bw2 shadow-5">
        <img alt="Robots" src={`https://robohash.org/${id}?200x200`}/>
        <div className='white'>
            <h2>{name}</h2>
            <p>{email}</p>
        </div>
        </div>
    </Fragment>
);
}

export default Cards;
