import React, { Fragment } from 'react';

const SearchBox = ({ searchChange }) => {
    return (
        <Fragment>
            <div className='pa2'>
                <input
                    className='pa3 ba b--green bg-light-green'
                    type='search'
                    placeholder='search robots'
                    onChange={searchChange}
                />
            </div>
        </Fragment>
    );
}

export default SearchBox;